<template>
	<div id="printer-list">
		<public-header title="打印机列表">
			<router-link tag="div" to="/pages/printer/post" slot="right" class="addPrinter-btn">
				<span class="icon icon-add"></span>
			</router-link>
		</public-header>
		<div class="content">
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<div class="printer-group" v-if="records.length > 0">
				<div class="printer-item" v-for="(item, index) in records">
					<div class="printer-title">
						<div class="flex-lr">
							<div class="font-bold flex">{{item.name}} <div class="printer-name">{{item.type_cn}}</div></div>
							<div class="itag itag-info" v-if="item.status == 1">启用</div>
							<div class="itag itag-danger" v-else-if="item.status == 0">停用</div>
						</div>
						<div class="font-14 c-gray padding-10-t">工作状态：{{item.status_cn}}</div>
						<div class="font-14 c-gray padding-10-t">打印联数：{{item.print_nums}}</div>
					</div>
					<div class="border-1px-t btn-group">
						<router-link tag="div" :to="util.getUrl({path: '/pages/printer/post', query:{id: item.id}})" class="btn-item bg-info">编辑</router-link>
						<div class="btn-item bg-default" @click="onDeletePrinter(item, index)">删除</div>
					</div>
				</div>
			</div>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				isRefresh: false,
				showPreLoading: true,
				records: {}
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					url: 'manage/shop/printer/list',
					success: (res) => {
						this.isRefresh = false;
						this.records = res.records;
					}
				});
			},
			onDeletePrinter(item, index){
				var that = this;
				that.util.jspost({
					url: 'manage/shop/printer/del',
					data: {
						id: item.id
					},
					confirm: '确定删除该打印机吗？',
					success: function(){
						that.records.splice(index, 1);
					}
				});
			},
			onPullDownRefresh(){
				this.onLoad();
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#printer-list .addPrinter-btn{
		height: 45px;
	}
	#printer-list .addPrinter-btn .icon-add{
		font-size: 20px;
	}
	#printer-list .content .printer-item{
		margin: 10px;
		background-color: #fff;
		border-radius: 5px;
	}
	#printer-list .content .printer-item .printer-title{
		padding: 10px;
	}
	#printer-list .content .printer-item .printer-title .printer-name{
		font-size: 12px;
		color: #fff;
		background-color: #FF8D2F;
		padding: 1px 10px;
		border-radius: 10px;
		margin-left: 10px;
	}
	#printer-list .content .printer-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#printer-list .content .printer-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


